const sortByPrice = (arrSort = [], sortType) => {
    const newArr = [...arrSort];
    //clone ra 1 mng mi trc khi tin hng sp xp
    // khi sortType l mc nh
    //hm sortByPrice tr v vn l 1 mng mi khc vi mng arrSort a vo ban u
    //nhng vy s m bo c s render trong component
    if(sortType !== 'default'){
        newArr.sort((a, b) => {
            if(sortType === 'ascending'){
                return a.price - b.price;
            }
            else if(sortType === 'descending'){
                return b.price - a.price;
            }
            return newArr
        })
    }
    return newArr
};
//https://niithanoi.edu.vn/sort-trong-javascript.html
export default sortByPrice;