const showRating = rate =>{
    const roundHalf = (parseFloat(rate) * 2).toFixed() / 2;
    //console.log('rate: ', rate);
    //console.log('roundHalf: ', roundHalf);

    let result = [];
    for(let i = 1 ; i <= roundHalf; i++){
        result.push(<i className="fas fa-star" key={i}></i>)
    };
    //vd: roundHalf = 3.5,  s chy 3 vng lp vi i bt u bng 1
    //v sang vng lp th 4, i = 4 v i > roundHalf (khng tha iu kin)
    //==> break vng lp
    //nu bt u vi i = 0, th s b chy ti 4 vng lp,
    //do vng lp th 4, i = 3 vn tha iu kin i <= roundHalf
    //==> khng ng vi set up rating

    if(!Number.isInteger(roundHalf)){
        //kim tra roundHalf c phi l s nguyn hay khng
        //trng hp lm trn thnh 0.5 ch c 2 trng hp s nguyn v s l
        //nu l s l th mi push 1 ci ngi sao phn na
        result.push(<i className="fas fa-star-half-alt" key={result.length + 1}></i>)
    };

    const remain = 5 - result.length;
    //mng rating s c 5 v tr ngi sao, nn ngi sao rng s chy lp y cc v tr cn li
    //nu gn trc tip vo vng lp for (5 - result.length) th mi ln chy mng result li thay i gi tr ca length
    //vng lp for s xt li iu kin mi dn n vic s chy ra khng  5 ngi sao
    //for(let i = 0; i < (5 - result.length); i++){
    for(let i = 0; i < remain; i++){
        result.push(<i className="far fa-star" key={result.length + 1}></i>)
    };    
    //console.log('rating by stars: ', result);
    return result;
};

export default showRating;